<template>
  <div class="mybooks">
    <BookTitle>我的书架</BookTitle>
    <ul>
      <van-swipe-cell right-width="100" v-for="(book, index) in mybooks" :key="book._id">
        <div class="selectContainer">
          <div class="cover">
            <van-image width="60" height="80" :src="book.cover | parseImageUrl" />
          </div>
          <div class="right">
            <h4 class="name">{{ book.title }}</h4>
            <p>阅读到第{{ 1 + index++ }}章</p>
          </div>
          <div class="toDetail" style="" @click="$router.push('/detail?id=' + book._id)"></div>
        </div>
        <template #right>
          <van-button
            square
            text="删除"
            type="danger"
            class="delete-button"
            @click="removeMybooks(book._id)"
          />
        </template>
      </van-swipe-cell>
    </ul>
  </div>
</template>

<script>
import BookTitle from "@/components/BookTitle.vue";
import { mapState, mapMutations } from "vuex";

export default {
  components: {
    BookTitle,
  },
  data() {
    return {};
  },
  filters: {
    parseImageUrl: function(n) {
      return decodeURIComponent(n.replace(/\/agent\//gi, ""));
    },
  },
  computed: {
    ...mapState(["mybooks"]),
  },
  methods: {
    ...mapMutations(["removeMybooks"]),
  },
};
</script>

